<template>
  <div class="my-popups"> 
    <loader :is-visible="isLoading"></loader>    
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header theme-primary partition-full">
            <h4 class="modal-title">{{$t('AddTag')}}</h4>
            <button type="button" class="close" v-on:click="CloseExpenseTag()">
              <em class="fa fa-times" aria-hidden="true"></em>
            </button>
          </div>
          <div class="modal-body">           
              <dynamic-form v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit"></dynamic-form>
              <div class="col-lg-6">
                        <div class="py-2"><small class="text-danger">{{$t('MandatoryString')}}</small></div>
              </div>
          </div>              
        </div>
      </div>
    </div>
  </div>

</template>
<script>
import ExpenseDataService from "@/services/DataService";
export default {
    data(){
        return{
            isLoading:false,            
            DataLoaded:0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [                     
                        {
                            astype: "TextField",
                            label: this.$t('TagName'),
                            name: "TagName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('STATUS_ID'),
                            name: "status",
                            value: "1001",
                            placeholder: "",
                            config: {
                                options: [{value:'1001',name:'Active'},{value:'1002',name:'InActive'}]
                            }
                        }
                    ],
                  },
                  {
                    layoutType: "Single",
                    Data:[
                      {
                      astype: "TextAreaField",
                        label: this.$t('TagDescription'),
                        name: "TagDescription",
                        value: "",
                        placeholder: "",
                        validationRules: { "required": true},
                        config: {
                          rows:5
                        }
                      }
                   ]
                }
            ],
            buttons: [               
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"                    
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseExpenseTag
                }
                
            ]
        }
    },
    created: function () { 
        this.DataLoaded=1;
    },
    methods:{
        CloseExpenseTag:function(){
            this.$emit('CloseExpenseTag');
        },
        OnSubmit:function(formfields) {
            var vm = this;
            vm.isLoading=true; 
            var jsonobj = {  
                TagId:0,           
                TagName: formfields.TagName,               
                TagDesc:formfields.TagDescription,
                ModuleName:'Expenses',
                StatusID:formfields.status
            };
            var jsonobj = JSON.stringify(jsonobj);                       
            ExpenseDataService.SaveTag(jsonobj).then((response)=>{                 
                 if(response.data[0].Status == 'Success')
                 {
                    vm.$emit('CloseExpenseTag');
                    vm.$parent.GetTagNameDDL();                                
                    vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('Tag')]), "success", true,  vm.$t("Alert"));
                 }else{
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Tag')]), "warning", true, vm.$t("Alert"));
                 }
                 vm.isLoading=false;
            });               
            vm.isLoading=false;           
        }      
    }
}
</script>
